.flex-pannels-body {
  background: #ffc600;
  margin: 0;
  font-size: 20px;
  font-weight: 200;
}

.panels {
  min-height: 100vh;
  display: flex;
  overflow: hidden;
}

.panel {
  color: #fff;
  text-align: center;
  background: #6b0f9c center / cover;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  transition: font-size .5s ease-out, flex-grow .5s ease-out, background .5s ease-out;
  display: flex;
  box-shadow: inset 0 0 0 5px rgba(255, 255, 255, .1);
}

.panel-1 {
  background-image: url("pexels-adrian-lang-2825368.a3d954f5.jpg");
}

.panel-2 {
  background-image: url("pexels-davyd-bortnik-3032039.c4c66c07.jpg");
}

.panel-3 {
  background-image: url("pexels-suliman-sallehi-1576939.2f57c7a7.jpg");
}

.panel-4 {
  background-image: url("pexels-tobias-bjørkli-1819634.bff2b435.jpg");
}

.panel-5 {
  background-image: url("pexels-s-migaj-951076.ffaab9fc.jpg");
}

.panel-6 {
  background-image: url("pexels-tobias-bjørkli-1819663.cb536a97.jpg");
}

.panel > * {
  width: 100%;
  flex: 1 0 auto;
  justify-content: center;
  align-items: center;
  margin: 0;
  transition: transform .5s;
  display: flex;
}

.panel > :first-child {
  transform: translateY(-100%);
}

.panel.open-active > :first-child {
  transform: translateY(0);
}

.panel > :last-child {
  transform: translateY(100%);
}

.panel.open-active > :last-child {
  transform: translateY(0);
}

.panel p {
  text-transform: uppercase;
  text-shadow: 0 0 4px rgba(0, 0, 0, .72), 0 0 14px rgba(0, 0, 0, .45);
  font-family: Playfair Display, serif;
  font-size: 2em;
  font-weight: 400;
}

.panel p:nth-child(2) {
  font-size: 4em;
}

.panel.open {
  flex: 5;
  font-size: 40px;
}

.cta {
  color: #fff;
  text-decoration: none;
}

